
body {
	width: 750px;
	margin: 0 auto;
	background: #F5F5F5;
}

.headers{
	position: fixed;
	top: 0;
	width: 750px;
	line-height: 80px;
	text-align: center;
	background: #42B983;
	color: #FFFFFF;
	font-size: 20px;
	font-weight: 600;
}

.todo[data-v-6235cee8]{
	width: 80%;
	margin: 100px auto;
	background: #FFFFFF;
	-webkit-box-shadow: -1px 4px 13px 1px #dcdcdc;
	        box-shadow: -1px 4px 13px 1px #dcdcdc;
	padding: 20px;
}
.add-input[data-v-6235cee8]{
	width: 100%;
	height: 40px;
	line-height: 40px;
	text-indent: 20px;
}
.items[data-v-6235cee8]{
	margin-top: 20px;
	background: #FFFFFF;
}

.todo-item {
	width: 100%;
	position: relative;
	line-height: 40px;
	height: 40px;
	background: #FFFFFF;
	color: #42B983;
	margin-top: 10px;
	-webkit-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, .4);
	        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, .4);
	overflow: hidden;
}
.todo-content {
	text-align: center;
	overflow: hidden;
	text-indent: 30px;
}
.todo-item:last-child {
	border: none !important;
}
.destory {
	width: 40px;
	height: 40px;
	position: absolute;
	right: 0;
	top: 0;
	background: rgba(251, 27, 27, .8) !important;
	color: #FFFFFF;
	border: none;
	overflow: hidden;
	outline: none;
}
.close {
	position: relative;
	width: 0.3em;
	height: 1.5em;
	background: #FFFFFF;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	display: inline-block;
}
.close:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 0.3em;
	height: 1.5em;
	background: #FFFFFF;
	-webkit-transform: rotate(270deg);
	transform: rotate(270deg);
}
.toggle {
	width: 20px;
	height: 20px;
	border-radius: 50%;
}
input[type="checkbox"] {
	position: absolute;
	top: 8px;
	left: 5px;
	background: #42B983;
	border: none !important;
	color: #FFFFFF;
}

.helper{
	width: 100%;
	line-height: 50px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.helper p{
	-webkit-box-flex: 0;
	    -ms-flex: 0 1 30%;
	        flex: 0 1 30%;
}
.tabs{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-flex: 0 !important;
	    -ms-flex: 0 1 40% !important;
	        flex: 0 1 40% !important;
	height: 50px;
}
.tabs span{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	width: 100%;
	height: 40px;
	display: block;
	text-align: center;
	margin: 2px;
	line-height: 40px;
	cursor: pointer;
}
.actived{
	border-radius: 5px;
	background: #42B983;
	color: #FFFFFF;
}
.clear{
	text-align: end;
	cursor: pointer;
	color: red;
}
